.card {
  position: relative;
  display: flex;
  flex-flow: column wrap;
  border: 1px solid rgba(182, 182, 182, 0.377);
  box-shadow: 1px 1px 20px rgba(49, 49, 49, 0.588);
  background-color: #fff;
}

.card .img {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 280px;
  max-width: 400px;
  overflow: hidden;
  margin-bottom: 10px;
}

.card .img img {
  height: 100%;
  width: 100%;
  transition: all 1.2s ease;
  opacity: 0.9;
}

.card .img img:hover {
  transform: scale(1.2);
  transition: all 1.2s ease;
  opacity: 1;
}

.card h4,
.card span {
  padding: 2px 15px;
}

.card span {
  color: rgb(102, 102, 102);
  margin-bottom: 10px;
}

.card span i {
  margin-right: 5px;
  color: #0d639c;
}
